"use client";

import { useEffect, useState, useMemo } from "react";
import { AppShell } from "@/components/layout/app-shell";
import { GlassCard } from "@/components/layout/glass-card";
import { ThemeSwitcher } from "@/components/theme-switcher";

type HaEntity = {
  entity_id: string;
  state: string;
  attributes: Record<string, unknown>;
};

function getDomain(entityId: string): string {
  return entityId.split(".")[0] ?? "";
}

export default function SettingsPage() {
  const [baseUrl, setBaseUrl] = useState("http://homeassistant.local:8123");
  const [token, setToken] = useState("");
  const [testing, setTesting] = useState(false);
  const [saving, setSaving] = useState(false);
  const [testResult, setTestResult] = useState<{ ok: true } | { ok: false; error: string } | null>(null);
  const [saveMessage, setSaveMessage] = useState<"success" | "error" | null>(null);
  const [entities, setEntities] = useState<HaEntity[]>([]);
  const [entitiesLoading, setEntitiesLoading] = useState(false);
  const [entitiesError, setEntitiesError] = useState<string | null>(null);

  useEffect(() => {
    fetch("/api/ha/connection")
      .then((r) => r.json())
      .then((d) => {
        if (d?.baseUrl) setBaseUrl(d.baseUrl);
      })
      .catch(() => {});
  }, []);

  function loadEntities() {
    setEntitiesError(null);
    setEntitiesLoading(true);
    fetch("/api/ha/entities")
      .then((r) => {
        if (!r.ok) return r.json().then((body) => ({ error: body?.error ?? "Ophalen mislukt" }));
        return r.json();
      })
      .then((data) => {
        if ("error" in data) {
          setEntitiesError(data.error);
          setEntities([]);
        } else if (Array.isArray(data)) {
          setEntities(data);
        }
      })
      .catch(() => {
        setEntitiesError("Kon entiteiten niet ophalen.");
        setEntities([]);
      })
      .finally(() => setEntitiesLoading(false));
  }

  useEffect(() => {
    loadEntities();
  }, [saveMessage]);

  const byDomain = useMemo(() => {
    const map = new Map<string, HaEntity[]>();
    for (const e of entities) {
      const domain = getDomain(e.entity_id);
      if (!map.has(domain)) map.set(domain, []);
      map.get(domain)!.push(e);
    }
    const sorted = Array.from(map.entries()).sort((a, b) => a[0].localeCompare(b[0]));
    return sorted;
  }, [entities]);

  async function handleTest() {
    setTestResult(null);
    setTesting(true);
    try {
      const res = await fetch("/api/ha/test", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ baseUrl, token }),
      });
      const data = await res.json();
      if (data.ok) setTestResult({ ok: true });
      else setTestResult({ ok: false, error: data.error ?? "Verbinding mislukt" });
    } catch (err) {
      setTestResult({ ok: false, error: err instanceof Error ? err.message : "Request mislukt" });
    } finally {
      setTesting(false);
    }
  }

  async function handleSave() {
    if (!token.trim()) {
      setTestResult({ ok: false, error: "Vul een token in." });
      return;
    }
    setSaveMessage(null);
    setSaving(true);
    try {
      const res = await fetch("/api/ha/connection", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ baseUrl, token }),
      });
      const data = await res.json();
      if (data.connectionId) {
        setSaveMessage("success");
        setTestResult({ ok: true });
      } else {
        setSaveMessage("error");
        setTestResult({ ok: false, error: data.error ?? "Opslaan mislukt" });
      }
    } catch {
      setSaveMessage("error");
      setTestResult({ ok: false, error: "Opslaan mislukt" });
    } finally {
      setSaving(false);
    }
  }

  return (
    <AppShell activeTab="/settings">
      <div className="space-y-6 max-w-2xl">
        <h2 className="text-xl font-semibold">Configuratie</h2>

        <GlassCard>
          <h3 className="text-card-title font-medium mb-2">Weergave</h3>
          <p className="text-sm text-gray-600 dark:text-gray-400 mb-3">
            Kies het thema voor de app (automatisch, licht of donker).
          </p>
          <ThemeSwitcher />
        </GlassCard>

        <GlassCard>
          <h3 className="text-card-title font-medium mb-3">Home Assistant-verbinding</h3>
          <p className="text-sm text-gray-600 dark:text-gray-400 mb-4">
            Vul het adres van je Home Assistant-instance in en een Long-Lived Access Token.
            Maak een token aan via Profiel → Long-Lived Access Tokens in Home Assistant.
          </p>
          <div className="space-y-4">
            <div>
              <label htmlFor="ha-baseUrl" className="block text-sm font-medium mb-1">
                Base URL
              </label>
              <input
                id="ha-baseUrl"
                type="url"
                value={baseUrl}
                onChange={(e) => setBaseUrl(e.target.value)}
                placeholder="http://homeassistant.local:8123"
                className="w-full rounded-lg border border-gray-300 dark:border-white/20 bg-white dark:bg-white/5 px-3 py-2 text-sm"
              />
            </div>
            <div>
              <label htmlFor="ha-token" className="block text-sm font-medium mb-1">
                Long-Lived Access Token
              </label>
              <input
                id="ha-token"
                type="password"
                value={token}
                onChange={(e) => setToken(e.target.value)}
                placeholder="Token (wordt niet getoond als er al een verbinding is)"
                className="w-full rounded-lg border border-gray-300 dark:border-white/20 bg-white dark:bg-white/5 px-3 py-2 text-sm"
              />
            </div>
            {testResult && (
              <div
                className={`rounded-lg p-3 text-sm ${
                  testResult.ok
                    ? "bg-green-50 dark:bg-green-950/30 text-green-800 dark:text-green-200"
                    : "bg-red-50 dark:bg-red-950/30 text-red-800 dark:text-red-200"
                }`}
              >
                {testResult.ok ? "Verbinding geslaagd." : testResult.error}
              </div>
            )}
            {saveMessage === "success" && (
              <p className="text-sm text-green-600 dark:text-green-400">Verbinding opgeslagen.</p>
            )}
            <div className="flex gap-2">
              <button
                type="button"
                onClick={handleTest}
                disabled={testing}
                className="rounded-full bg-accent-yellow dark:bg-accent-green px-4 py-2 text-sm font-medium text-gray-900 disabled:opacity-50"
              >
                {testing ? "Testen…" : "Test verbinding"}
              </button>
              <button
                type="button"
                onClick={handleSave}
                disabled={saving || !token.trim()}
                className="rounded-full bg-white dark:bg-white/10 border border-gray-200 dark:border-white/20 px-4 py-2 text-sm font-medium disabled:opacity-50"
              >
                {saving ? "Opslaan…" : "Opslaan"}
              </button>
            </div>
          </div>
        </GlassCard>

        <GlassCard>
          <h3 className="text-card-title font-medium mb-2">Entiteiten per type</h3>
          <p className="text-sm text-gray-600 dark:text-gray-400 mb-3">
            Overzicht van entiteiten uit Home Assistant, gegroepeerd per type (domain).
            Sla eerst een verbinding op om deze lijst te laden.
          </p>
          {entitiesLoading ? (
            <p className="text-sm text-gray-500">Entiteiten ophalen…</p>
          ) : entitiesError ? (
            <p className="text-sm text-amber-600 dark:text-amber-400">{entitiesError}</p>
          ) : byDomain.length === 0 ? (
            <p className="text-sm text-gray-500">Geen entiteiten of nog geen verbinding opgeslagen.</p>
          ) : (
            <>
              <button
                type="button"
                onClick={loadEntities}
                className="mb-3 text-sm text-accent-purple dark:text-accent-green hover:underline"
              >
                Vernieuwen
              </button>
              <div className="space-y-4 max-h-[60vh] overflow-auto">
                {byDomain.map(([domain, list]) => (
                  <div key={domain}>
                    <h4 className="text-sm font-semibold text-gray-700 dark:text-gray-200 mb-2 capitalize">
                      {domain}
                      <span className="ml-2 font-normal text-gray-500 dark:text-gray-400">
                        ({list.length})
                      </span>
                    </h4>
                    <ul className="space-y-1 rounded-lg border border-gray-200 dark:border-white/10 bg-gray-50/50 dark:bg-white/5 p-2">
                      {list.map((e) => {
                        const name =
                          (e.attributes?.friendly_name as string) ?? e.entity_id;
                        return (
                          <li
                            key={e.entity_id}
                            className="flex items-center justify-between gap-2 text-sm py-1 px-2 rounded hover:bg-gray-100 dark:hover:bg-white/10"
                          >
                            <span className="truncate font-medium" title={e.entity_id}>
                              {name}
                            </span>
                            <span className="text-xs text-gray-500 dark:text-gray-400 shrink-0">
                              {e.state}
                            </span>
                          </li>
                        );
                      })}
                    </ul>
                  </div>
                ))}
              </div>
            </>
          )}
        </GlassCard>
      </div>
    </AppShell>
  );
}
